"use client";

import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import {
  Sidebar,
  SidebarContent,
  SidebarFooter,
  SidebarGroup,
  SidebarGroupContent,
  SidebarGroupLabel,
  SidebarHeader,
  SidebarMenu,
  SidebarMenuBadge,
  SidebarMenuButton,
  SidebarMenuItem,
} from "@/components/ui/sidebar";
import { cn } from "@/lib/utils";
import {
  AlertTriangle,
  Bot,
  Filter,
  MessageSquare,
  Search,
  UserCheck,
} from "lucide-react";
import { useState } from "react";

// Dummy conversation data
const conversations = [
  {
    id: "1",
    name: "Sarah Johnson",
    avatar: "/avatars/sarah.jpg",
    lastMessage: "I need help with my hotel booking...",
    time: "2m ago",
    unread: 3,
    status: "active" as const,
    isAiHandling: false,
  },
  {
    id: "2",
    name: "Mike Chen",
    avatar: "/avatars/mike.jpg",
    lastMessage: "The AI suggested I contact support",
    time: "5m ago",
    unread: 1,
    status: "escalated" as const,
    isAiHandling: false,
  },
  {
    id: "3",
    name: "Emily Davis",
    avatar: "/avatars/emily.jpg",
    lastMessage: "Thank you for your help!",
    time: "12m ago",
    unread: 0,
    status: "active" as const,
    isAiHandling: true,
  },
  {
    id: "4",
    name: "James Wilson",
    avatar: "/avatars/james.jpg",
    lastMessage: "Can I change my reservation date?",
    time: "18m ago",
    unread: 0,
    status: "waiting" as const,
    isAiHandling: true,
  },
  {
    id: "5",
    name: "Lisa Anderson",
    avatar: "/avatars/lisa.jpg",
    lastMessage: "This is urgent! Please help ASAP",
    time: "25m ago",
    unread: 5,
    status: "urgent" as const,
    isAiHandling: false,
  },
  {
    id: "6",
    name: "Robert Taylor",
    avatar: "/avatars/robert.jpg",
    lastMessage: "I have a question about amenities",
    time: "1h ago",
    unread: 0,
    status: "active" as const,
    isAiHandling: true,
  },
];

const statusConfig = {
  active: {
    label: "Active",
    color: "bg-emerald-500",
    textColor: "text-emerald-600 dark:text-emerald-400",
  },
  waiting: {
    label: "Waiting",
    color: "bg-amber-500",
    textColor: "text-amber-600 dark:text-amber-400",
  },
  escalated: {
    label: "Escalated",
    color: "bg-rose-500",
    textColor: "text-rose-600 dark:text-rose-400",
  },
  urgent: {
    label: "Urgent",
    color: "bg-rose-500 animate-pulse",
    textColor: "text-rose-600 dark:text-rose-400",
  },
};

export function ConversationsSidebar() {
  const [selectedId, setSelectedId] = useState("1");
  const [searchQuery, setSearchQuery] = useState("");
  const [filter, setFilter] = useState("all");

  const filteredConversations = conversations.filter((conv) => {
    const matchesSearch = conv.name
      .toLowerCase()
      .includes(searchQuery.toLowerCase());
    const matchesFilter =
      filter === "all" ||
      (filter === "urgent" && conv.status === "urgent") ||
      (filter === "escalated" && conv.status === "escalated") ||
      (filter === "ai" && conv.isAiHandling) ||
      (filter === "human" && !conv.isAiHandling);
    return matchesSearch && matchesFilter;
  });

  return (
    <Sidebar
      variant="sidebar"
      collapsible="icon"
      className="border-r border-sidebar-border"
    >
      <SidebarHeader className="border-b border-sidebar-border pb-4">
        <div className="flex items-center gap-2 px-2">
          <MessageSquare className="size-5 text-emerald-500" />
          <span className="font-semibold text-sm group-data-[collapsible=icon]:hidden">
            Conversations
          </span>
          <span className="ml-auto text-xs font-medium px-2 py-0.5 rounded-full bg-muted group-data-[collapsible=icon]:hidden">
            {conversations.length}
          </span>
        </div>
        <div className="space-y-2 group-data-[collapsible=icon]:hidden">
          <div className="relative">
            <Search className="absolute left-2.5 top-1/2 -translate-y-1/2 size-4 text-muted-foreground" />
            <Input
              placeholder="Search conversations..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="pl-8 h-8 text-sm bg-sidebar-accent/50"
            />
          </div>
          <Select value={filter} onValueChange={setFilter}>
            <SelectTrigger className="h-8 text-xs">
              <Filter className="size-3 mr-2" />
              <SelectValue placeholder="Filter" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Conversations</SelectItem>
              <SelectItem value="urgent">Urgent Only</SelectItem>
              <SelectItem value="escalated">Escalated</SelectItem>
              <SelectItem value="ai">AI Handling</SelectItem>
              <SelectItem value="human">Human Handling</SelectItem>
            </SelectContent>
          </Select>
        </div>
      </SidebarHeader>

      <SidebarContent>
        <SidebarGroup>
          <SidebarGroupLabel className="text-xs uppercase tracking-wider text-muted-foreground">
            Active Chats
          </SidebarGroupLabel>
          <SidebarGroupContent>
            <SidebarMenu>
              {filteredConversations.map((conversation) => (
                <SidebarMenuItem key={conversation.id}>
                  <SidebarMenuButton
                    isActive={selectedId === conversation.id}
                    onClick={() => setSelectedId(conversation.id)}
                    className="h-auto py-3 px-2"
                    tooltip={conversation.name}
                  >
                    <div className="relative shrink-0">
                      <Avatar className="size-9">
                        <AvatarImage
                          src={conversation.avatar}
                          alt={conversation.name}
                        />
                        <AvatarFallback className="text-xs bg-gradient-to-br from-slate-600 to-slate-700 text-white">
                          {conversation.name
                            .split(" ")
                            .map((n) => n[0])
                            .join("")}
                        </AvatarFallback>
                      </Avatar>
                      <span
                        className={cn(
                          "absolute -bottom-0.5 -right-0.5 size-3 rounded-full border-2 border-sidebar",
                          statusConfig[conversation.status].color
                        )}
                      />
                    </div>
                    <div className="flex-1 min-w-0 group-data-[collapsible=icon]:hidden">
                      <div className="flex items-center justify-between gap-2">
                        <span className="font-medium text-sm truncate">
                          {conversation.name}
                        </span>
                        <span className="text-[10px] text-muted-foreground shrink-0">
                          {conversation.time}
                        </span>
                      </div>
                      <div className="flex items-center gap-1.5 mt-0.5">
                        {conversation.isAiHandling ? (
                          <Bot className="size-3 text-violet-500 shrink-0" />
                        ) : (
                          <UserCheck className="size-3 text-emerald-500 shrink-0" />
                        )}
                        <p className="text-xs text-muted-foreground truncate">
                          {conversation.lastMessage}
                        </p>
                      </div>
                      <div className="flex items-center gap-1.5 mt-1">
                        {conversation.status === "urgent" && (
                          <span className="flex items-center gap-1 text-[10px] font-medium text-rose-500">
                            <AlertTriangle className="size-3" />
                            Urgent
                          </span>
                        )}
                        {conversation.status === "escalated" && (
                          <span className="text-[10px] font-medium px-1.5 py-0.5 rounded bg-rose-500/10 text-rose-500">
                            Escalated
                          </span>
                        )}
                      </div>
                    </div>
                  </SidebarMenuButton>
                  {conversation.unread > 0 && (
                    <SidebarMenuBadge className="bg-emerald-500 text-white text-[10px] font-bold">
                      {conversation.unread}
                    </SidebarMenuBadge>
                  )}
                </SidebarMenuItem>
              ))}
            </SidebarMenu>
          </SidebarGroupContent>
        </SidebarGroup>
      </SidebarContent>

      <SidebarFooter className="border-t border-sidebar-border group-data-[collapsible=icon]:hidden">
        <div className="flex items-center justify-between text-xs text-muted-foreground px-2">
          <span>
            {conversations.filter((c) => c.isAiHandling).length} AI Handled
          </span>
          <span>
            {conversations.filter((c) => !c.isAiHandling).length} Human Handled
          </span>
        </div>
      </SidebarFooter>
    </Sidebar>
  );
}

